//     fetch-
const createFetchOptions = (options: RequestInit = {}): RequestInit => {
  const defaultOptions: RequestInit = {
    credentials: 'include',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json; charset=utf-8',
      //     
      'X-Requested-With': 'XMLHttpRequest',
    },
    //      Telegram WebApp
    mode: 'cors',
    ...options
  };

  //     
  if (options.headers) {
    defaultOptions.headers = {
      ...defaultOptions.headers,
      ...options.headers
    };
  }

  return defaultOptions;
};

//    
interface ApiResponse<T = any> {
  data: T | null;
  error: { message: string } | null;
  status: number;
}

//   API
interface TextApiResponse {
  message: string;
}

//     
const fetchWithRetry = async <T = any>(url: string, options: RequestInit = {}, retries = 1): Promise<ApiResponse<T>> => {
  try {
    //      
    const fetchOptions = createFetchOptions(options);
    
    //     
    console.log(`[API Request] ${options.method || 'GET'} ${url}`, { options: fetchOptions });
    
    //    
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 15000); // 15- 
    
    const startTime = Date.now();
    const response = await fetch(url, {
      ...fetchOptions,
      signal: controller.signal
    });
    const requestTime = Date.now() - startTime;
    
    clearTimeout(timeoutId);
    
    //   
    console.log(`[API Response] ${response.status} ${url} (${requestTime}ms)`, {
      ok: response.ok,
      status: response.status,
      statusText: response.statusText,
      headers: Object.fromEntries(Array.from(response.headers.entries()))
    });
    
    //   ,  
    if (response.ok) {
      const contentType = response.headers.get('content-type');
      
      // ,   - JSON
      if (contentType && contentType.includes('application/json')) {
        const data = await response.json();
        return { data, error: null, status: response.status };
      } else {
        //    JSON,  
        const text = await response.text();
        try {
          //    JSON,      Content-Type
          const jsonData = JSON.parse(text);
          console.log(`[API Warning] Response has content-type "${contentType}" but contains valid JSON`);
          return { data: jsonData as T, error: null, status: response.status };
        } catch (e) {
          //    message   
          console.log(`[API Warning] Non-JSON response from server: ${text.substring(0, 100)}...`);
          return { data: { message: text } as unknown as T, error: null, status: response.status };
        }
      }
    }
    
    //  401/403, ,   
    if (response.status === 401 || response.status === 403) {
      console.error(`Auth error: ${response.status}`);
      //     
      return { 
        data: null, 
        error: { message: 'Authentication required' }, 
        status: response.status 
      };
    }
    
    //        
    let errorMessage;
    try {
      const errorData = await response.json();
      errorMessage = errorData.detail || errorData.message || 'Unknown error';
    } catch (e) {
      errorMessage = await response.text() || `HTTP error: ${response.status}`;
    }
    
    return { 
      data: null, 
      error: { message: errorMessage }, 
      status: response.status 
    };
    
  } catch (error: any) { //   any   error
    //      
    console.error(`[API Error] Network error for ${url}: ${error.message || 'Unknown error'}`);
    
    //       (timeout)
    if (error.name === 'AbortError') {
      console.error(`[API Timeout] Request timeout for ${url}`);
      return { 
        data: null, 
        error: { message: 'Request timed out' }, 
        status: 0 
      };
    }
    
    //      
    if (retries > 0) {
      console.log(`[API Retry] Retrying ${url}... ${retries} attempts left`);
      
      //     (    )
      await new Promise(resolve => setTimeout(resolve, (2 - retries) * 1000));
      
      //   
      return fetchWithRetry(url, options, retries - 1);
    }
    
    //    ,  
    return { 
      data: null, 
      error: { message: error.message || 'Network error' }, 
      status: 0 
    };
  }
};

//   
type CacheKey = string;
type CacheValue = any;
type CacheTimeStamp = number;

//  ,  
export interface City {
  id: number;
  name: string;
}

export interface Restaurant {
  id: number;
  name: string;
  city: number;
  address: string;
  description: string;
  cover_image: string;
  rating: number;
}

//   
class CacheService {
  private cache: Map<CacheKey, { value: CacheValue; timestamp: CacheTimeStamp }> = new Map();
  private readonly defaultExpiration = 5 * 60 * 1000; // 5 

  set(key: CacheKey, value: CacheValue, expiration: number = this.defaultExpiration): void {
    this.cache.set(key, {
      value,
      timestamp: Date.now() + expiration,
    });
  }

  get(key: CacheKey): CacheValue | null {
    const cached = this.cache.get(key);
    
    if (!cached) {
      return null;
    }

    if (Date.now() > cached.timestamp) {
      this.cache.delete(key);
      return null;
    }

    return cached.value;
  }

  invalidate(key: CacheKey): void {
    this.cache.delete(key);
  }

  invalidateAll(): void {
    this.cache.clear();
  }
}

const cacheService = new CacheService();

//    API
const handleApiError = (error: any, message: string) => {
  //   
  if (error.response) {
    console.error(`${message}:`, {
      status: error.response.status,
      data: error.response.data,
      message: error.message
    });
  } else if (error instanceof Error) {
    console.error(`${message}:`, error.message);
  } else {
    console.error(`${message}:`, error);
  }
  
  //        
  return {
    error: true,
    message: error.message || message,
    status: error.response?.status || 500,
    data: error.response?.data || null
  };
};

//      
function fixRussianEncoding(text: string): string {
  //     
  const replacements: Record<string, string> = {
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': '', '': '', '': '',
    '': '', '': '', '': '', '': '', '': ''
  };
  
  //    
  return text.replace(/[-]|.|.||||||||||||||/g, (match) => {
    return replacements[match] || match;
  });
}

